<template>
  <div class="container">
    <div class="forms w-50">
      <div class="row mb-1">
        <input class="col" placeholder="Enter fev predicted" v-model="fev_predicted" type="number">
        <input class="col" placeholder="Enter mMRC" v-model="mMRC" type="number">
      </div>
      <div class="row mt-1">
        <input class="col" placeholder="Enter BMI" v-model="BMI" type="number">
        <input class="col" placeholder="Enter MWD6" v-model="MWD6" type="number">
      </div>
    </div>
    <div class="result">
      Bode index for this patient is: {{ result }}
      <br>
      Bode index is from 0-10

    </div>
  </div>
</template>

<script>
export default {
  name: "Main",
  computed: {
    result() {
      return 0 * (this.BMI > 21) + 1 * (this.BMI <= 21)
          + 0 * (this.FEV_predicted >= 65) + 1 * (50 <= this.FEV_predicted <= 64)
          + 2 * (36 <= this.FEV_predicted <= 49) + 3 * (this.FEV_predicted <= 35)
          + 0 * (this.mMRC == 0) + 0 * (this.mMRC == 1) + 1 * (this.mMRC == 2) + 2 * (this.mMRC == 3)
          + 3 * (this.mMRC == 4)
          + 0 * (this.MWD6 >= 350) + 1 * (250 <= this.MWD6 <= 349) + 2 * (150 <= this.MWD6 <= 249)
          + 3 * (this.MWD6 <= 149)

    }
  },
  data() {
    return {
      fev_predicted: null,
      mMRC: null,
      BMI: null,
      MWD6: null
    }
  }

}
</script>

